import React from "react";
import PropTypes from "prop-types";
import "./Movie.css";

function Movie({ id, year, title, summary, poster , genres}) {
  return <div className="movies__movie">
      <h3 className="movie__title">{title}</h3>
      <ul className="movie__genres">
        {genres.map( (geners , index) => <li key={index} className="movie__genres__gener">  {geners} </li> )}        
      </ul>
      <h4 className="movie__year">{year}</h4>
      <p className="movie__summary">{summary.slice(0,160)} ... </p>
      <img className="movie__poster" src={poster} alt={title}/>
    </div>;
}

Movie.propTypes = {
  id: PropTypes.number.isRequired,
  year: PropTypes.number.isRequired,
  title: PropTypes.string.isRequired,
  summary: PropTypes.string.isRequired,
  genres: PropTypes.arrayOf(PropTypes.string).isRequired,
  poster: PropTypes.string.isRequired
};

export default Movie;